@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Inter-Medium.woff2");
}
@font-face {
  font-family: "Inter Bold";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/Inter-Bold.woff2");
}
@font-face {
  font-family: "Inter Black";
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/Inter-Black.woff2");
}
:root {
  --primary-color: #D8FF00;
  --secondary-color: #5E6F00;
  --tertiary-color: #B6D600;
  --white-color: #FFF;
  --black-color: #333333;
  --font-normal: "Inter", sans-serif;
  --font-bold: "Inter Bold", sans-serif;
  --font-black: "Inter Black", sans-serif;
  --leading-loose: 34px;
  --container: 1152px;
}

body {
  background-color: var(--primary-color);
  font-family: var(--font-normal);
  color: var(--black-color);
}

.wp-site-blocks {
  max-width: var(--container);
  margin: 0 auto;
}

h1 strong,
h2 strong,
h3 strong,
h4 strong {
  color: var(--tertiary-color);
}

p {
  font-family: var(--font-normal);
  line-height: var(--leading-loose);
}
p strong {
  font-family: var(--font-bold);
}

.alignwide,
.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw;
}

.ct-cards .wp-block-post-template .wp-block-post .ct-group {
  display: flex;
  min-height: 248px;
  border-radius: 4px;
}
.ct-cards .wp-block-post-template .wp-block-post .ct-group > .wp-block-group {
  align-content: space-between;
  display: flex;
  position: relative;
  width: 100%;
  margin-top: 0;
}
.ct-cards .wp-block-post-template .wp-block-post .ct-group > .wp-block-group:first-child {
  padding: 16px;
}
.ct-cards .wp-block-post-template .wp-block-post .ct-group .wp-block-post-title {
  color: var(--primary-color);
  font-family: var(--font-normal);
  font-weight: 500;
  padding: 0;
}
.ct-cards .wp-block-post-template .wp-block-post .ct-group .wp-block-post-terms {
  position: absolute;
  z-index: 10;
  right: 0;
  top: 0;
}
.ct-cards .wp-block-post-template .wp-block-post .ct-group .wp-block-post-featured-image,
.ct-cards .wp-block-post-template .wp-block-post .ct-group img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}